import {publicing} from '../../api/api.js'
import {wxloginurl} from '../../api/request.js'
let logins = require('../../login/login.js')
export default{
	data() {
		return {
			islogin: true,
			userInfo:{}
		}
	},
	methods:{
		getUserInfo(event){
			let wxing = event.detail.userInfo
			// 实例化类
			const logclass = new logins(wxing)
			// 实例化类之后。可以访问类中的方法和属性
			logclass.listing()
			.then((res)=>{
				this.islogin=true
				console.log(res)
			})
			.catch((err)=>{
				this.islogin=false
				console.log(err)
			})
			
		},
		
	},
	onShow() {
		console.log("进入触发onshow")
		let userInfo = uni.getStorageSync("userInfo")
		if(userInfo){
			this.islogin=true
			console.log('1')
			this.userInfo=userInfo
		}
		else{
			//调用登录方法
			this.islogin=false
			console.log('2')
		}
	}
}
